.work {
  padding: 95px 0 85px;

  &__image {
    position: relative;
    height: 320px;

    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      min-height: 100%;
      min-width: 100%;
      z-index: 1;
    }
  }

  &__description {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: $white;
    padding: 20px;
    background: rgba(192, 48, 28, 0.7);
    text-align: center;
    opacity: 0;
    transition: .3s;

    h6 {
      font-family: $font-montserrat;
      font-size: 18px;
      margin: 0 0 10px;
    }

    span {
      font-size: 12px;
      margin: 0 0 20px;
    }

    a {
      background: $white;
      transition: .3s;
      font-style: 14px;
      text-transform: uppercase;
      color: $red;
      padding: 12px 20px;
      display: flex;
      align-items: center;
      line-height: 1;

      &:hover {
        background: rgba(255, 255, 255, 0.85);
      }

      span {
        display: inline-block;
        vertical-align: top;
        width: 6px;
        height: 10px;
        background: url("../images/fa-angle-left.png") no-repeat 50%;
        background-size: 100% 100%;
        margin: 0 0 0 10px;
      }
    }
  }

  .col-33 {

    &:hover {

      .work__description {
        opacity: 1;
      }
    }
  }


}

.tabs {
  &__buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 45px;
  }

  &__button {
    font-size: 16px;
    font-family: $font-roboto;
    padding: 5px 14px;
    cursor: pointer;

    &.active {
      color: $red;
    }
  }

  &__item {
    display: none;
  }

  .col-33 {
    position: relative;
  }

}